.form-range { 
    &::-webkit-slider-thumb {
        cursor: pointer;
    }
}
.media-search {
    position: relative;
    .close-icon {
        position: absolute;
        left: auto;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }
}
